.checkout-item-container {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;
  
    .image-container {
      width: 23%;
      padding-right: 15px;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name,
    .quantity,
    .price {
      width: 23%;
    //   outline: 1px solid red;
    //   padding:0 30px;
    }
  
    .quantity {
        display: flex;
        // padding: 0 10px;
        .arrow {
            cursor: pointer;
            margin: 0;
            background-color: transparent;
            border: none;
            font-size: 18px;
        }
  
        .value {
            margin: 0 10px;
        }
    }
  
    .remove-button {
      padding-left: 12px;
      cursor: pointer;
      margin:0;
      background-color: transparent;
      border:none;
      font-size: 20px;
    }
  }
  